* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

body {
  background-color: thistle;
}

.list {
  max-width: 640px;
  width: calc(100% - 20%);
  height: 800px;
  margin: 20px auto;
  background-color: rgb(244, 229, 231);
  border-radius: 20%;
  border: 2px solid #b8a1b4;


}

.list h2 {
  text-align: center;
  font-size: 60px;
  margin: 50px;
  color: thistle;
}

.list form input {
  width: 600px;
  height: 100px;
  border-radius: 10px;
  border: 2px dashed #b8a1b4;
  display: block;
  margin: 0 auto;
}

.list form {
  position: relative;
}

.list form input::placeholder {
  color: rgb(160, 126, 155);
  font-weight: bold;
  font-size: 20px;
  text-align: center;
}

.list form button {
  display: flex;
  position: absolute;
  top: 10%;
  right: 0;
  transform: translate(-50%, 50%);

  border-radius: 50%;
  padding: 10px;
  border: 2px solid #b8a1b4;

  margin-right: 30px;

  cursor: pointer;

}

.list ul {
  text-align:center;
  color: rgb(160, 126, 155);
}

.list ul li {
  position: relative;

  border-bottom: 1px dashed #eee;
}

.list ul li span::before {
  content: 'ðŸ’¨';
  position: absolute;
  right: 20%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 99999;
}